@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.table th:first-child {
	position: sticky;
	position: -webkit-sticky;
	left: 0px;
	top: 0px;
	z-index: 11;
}

::-webkit-scrollbar {
	width: 4px;
	height: 4px;
}
::-webkit-scrollbar-track {
	background: #f1f1f100;
}
::-webkit-scrollbar-thumb {
	background: #888;
	border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
	background: #555;
}
